import React from "react";

const Banner03 = () => {
  return (
    <>
      <section className="hero-inner">
        <div
          className="hero-inner__bg hero-inner__bg_two"
          data-bg-image="assets/images/bg/inner-bg-6.jpg"
        >
          <div className="container">
            <div className="row">
              <div className="col-sm-12">
                <div className="page-breadcrumb">
                  <h1 className="heading">Dapp</h1>
                  <nav aria-label="breadcrumb">
                    <ol className="breadcrumb">
                      <li
                        className="breadcrumb-item active"
                        aria-current="page"
                      >
                        去中心化應用程式（Decentralized Application），相對於過往運行在中心化服務器的 App，Dapp 的程式部署在分佈式的網絡上，所有的數據皆公開透明且不可篡改
                      </li>
                    </ol>
                  </nav>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Banner03;
