import freemint from "../contracts/freemint.json";
import { ethers } from "ethers";
import ModalUtils from "../utils/modal.util";

const mintAbi = freemint.abi;
const contract = '0x7c931B3e9b00E98881C6070c33dd1E1777559E41';

/**
 * 初始化ETH
 * @param {*} contractAddress 
 * @param {*} abi 
 * @returns 
 */
const ethereumInit = (contractAddress, abi) => {
    const { ethereum } = window;
    if (!ethereum) {
        ModalUtils.confirm('ERROR', 'Please install Wallet!', () => { }, () => { });
        return null;
    }

    try {
        const { ethereum } = window;
        if (ethereum) {
            const provider = new ethers.providers.Web3Provider(ethereum);
            const signer = provider.getSigner();
            const nftContract = new ethers.Contract(contractAddress, abi, signer);
            return {nftContract, provider};
        } else {
            ModalUtils.confirm('ERROR', 'Please install Wallet!', () => { }, () => { });
            return null;
        }
    } catch (err) {
        if (String(err).indexOf('insufficient funds') > -1) {
            ModalUtils.confirm('ERROR', 'The wallet is not enough.', () => { }, () => { });
            return;
          }
          ModalUtils.confirm('ERROR', 'Cannot mint temporarily.', () => { }, () => { });
          return;
    }
}

/**
 * 链接钱包
 * @returns 
 */
export const connectWallet = async () => {
    const { ethereum } = window;

    if (!ethereum) {
        alert("Please install Metamask!");
        return false;
    }

    try {
        const accounts = await ethereum.request({ method: "eth_requestAccounts" });
        if (accounts != undefined && accounts != null && accounts != "") {
            console.log("Found an account! Address: ", accounts[0]);
            return accounts[0];
        }
        return false;
    } catch (err) {
        ModalUtils.confirm('ERROR', 'Can not connect to wallet!', () => { }, () => { });
        return false;
    }
};

/**
 * mint
 * @param {*} mintCount 
 * @param {*} price 
 */
export const mint = async (mintCount, _price) => {
    try {
        let price = {
            value: `${ethers.utils.parseEther(_price) * mintCount}`
        };
        console.log('price', price);
        const { nftContract, provider } = await ethereumInit(contract, mintAbi);
        try {
            if (nftContract != null) {
                let mintRslt = await nftContract.mint(mintCount, price);
                const txHash = mintRslt.hash;
                console.log(mintRslt);

                const receipt = await provider.waitForTransaction(txHash);
                console.log(receipt);

            }
        } catch (error) {
            if (String(error).indexOf('insufficient funds') > -1) {
                ModalUtils.confirm('ERROR', 'The wallet is not enough.', () => { }, () => { });
                return;
              }
              ModalUtils.confirm('ERROR', 'Cannot mint temporarily.', () => { }, () => { });
              return;
        }

    } catch (err) {
        if (String(err).indexOf('insufficient funds') > -1) {
            ModalUtils.confirm('ERROR', 'The wallet is not enough.', () => { }, () => { });
            return;
          }
          ModalUtils.confirm('ERROR', 'Cannot mint temporarily.', () => { }, () => { });
          return;
    }
};
